import {Component, ComponentInterface, Event, EventEmitter, h, Host, Prop, State, Watch} from '@stencil/core';
import {Converter} from 'showdown';
import {isDefined, isNotEmpty} from '../../utils/utils';
import DOMPurify from 'dompurify';

@Component({
  tag: 'soul-markdown-content',
  styleUrl: 'soul-markdown-content.scss',
  shadow: false,
})
export class SoulMarkdownContent implements ComponentInterface {

  @Prop({reflect: true}) public url: string;
  @Prop({reflect: false}) public value: string;
  @Prop({reflect: true}) public scale: 's' | 'm' | 'l' = 's';
  @Prop({reflect: true}) public trustContent = false;

  @Event() public soulMarkdownConverted: EventEmitter<string>;

  @State()
  private convertedValue: string;
  private converter: Converter;

  constructor() {
    this.converter = new Converter();
    this.converter.setOption('strikethrough', 'true');
    this.converter.setOption('tables', 'true');
    this.converter.setOption('tasklists', 'true');
    this.converter.setOption('simplifiedAutoLink', 'true');
  }

  @Watch('url')
  @Watch('value')
  @Watch('trustContent')
  public onDisabledChange() {
    this.updateInnerHTML();
  }

  public componentWillLoad(): Promise<void> | void {
    this.updateInnerHTML();
  }

  render() {
    return (
      <Host>
        <div class={{
          'soul-content': true,
          [`soul-font-scale-${this.scale}`]: true,
          'h-y-auto-scroll': true,
        }} innerHTML={isDefined(this.convertedValue) ? this.convertedValue : ''}>
        </div>
      </Host>
    );
  }

  private updateInnerHTML(): void {
    if (isNotEmpty(this.url)) {
      fetch(this.url)
        .then((response) => {
          if (response.ok) {
            return response.text();
          }
          throw new Error(response.statusText);
        })
        .then((text) => {
          this.convertedValue = this.convertMarkdownToHTML(text);
          this.soulMarkdownConverted.emit(this.convertedValue);
        })
        .catch(() => {
          this.convertedValue = '';
        });
    } else if (isNotEmpty(this.value)) {
      this.convertedValue = this.convertMarkdownToHTML(this.value);
      this.soulMarkdownConverted.emit(this.convertedValue);
    } else {
      this.convertedValue = '';
    }
  }

  private convertMarkdownToHTML(markdown: string): string {
    const html = this.converter.makeHtml(markdown);
    return this.trustContent ? html : DOMPurify.sanitize(html);
  }

}
